/* Custom theme CSS variables for winter theme - Brand Colors from Kwale Tech Hub Logo */
:root {
  /* Base colors - Clean whites and light grays */
  --color-base-100: oklch(99% 0 0);
  --color-base-200: oklch(96% 0.006 220);
  --color-base-300: oklch(91% 0.012 220);
  --color-base-content: oklch(25% 0.015 220);
  
  /* Brand Primary - Deep Blue from logo */
  --color-primary: oklch(0.55 0.18 230);
  --color-primary-content: oklch(98% 0.01 230);
  
  /* Brand Secondary - Golden Yellow from logo */
  --color-secondary: oklch(0.78 0.15 85);
  --color-secondary-content: oklch(20% 0.02 85);
  
  /* Brand Accent - Vibrant Orange/Red from logo */
  --color-accent: oklch(0.65 0.22 25);
  --color-accent-content: oklch(95% 0.02 25);
  
  /* Supporting colors */
  --color-neutral: oklch(0.32 0.02 220);
  --color-neutral-content: oklch(85% 0.01 220);
  --color-info: oklch(0.65 0.15 250);
  --color-info-content: oklch(15% 0.02 250);
  --color-success: oklch(0.70 0.18 155);
  --color-success-content: oklch(15% 0.02 155);
  --color-warning: oklch(0.80 0.16 75);
  --color-warning-content: oklch(20% 0.02 75);
  --color-error: oklch(0.65 0.20 15);
  --color-error-content: oklch(95% 0.02 15);
  
  /* Design tokens */
  --radius-selector: 1.2rem;
  --radius-field: 0.75rem;
  --radius-box: 1.2rem;
  --size-selector: 0.25rem;
  --size-field: 0.25rem;
  --border: 1px;
  --depth: 2px;
  --noise: 0;
}
 
