/* ========================================================================
   Component: ButtonGroup
   ======================================================================== */

/* Base container for the button group */
.ui\:btn-group,
.ui\:btn-group-horizontal {
  display: inline-flex;
  flex-direction: row;
}

.ui\:btn-group-vertical {
  display: inline-flex;
  flex-direction: column;
}

/* Remove the border radius from adjacent buttons to make them "stick" together.
  The :not(:first-child) and :not(:last-child) selectors ensure that the
  first and last buttons in the group keep their rounded corners on the outside.
*/

/* Horizontal Group Styling */
.ui\:btn-group-horizontal > .ui\:btn:not(:first-child) {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  margin-left: -1px; /* Overlap borders */
}

.ui\:btn-group-horizontal > .ui\:btn:not(:last-child) {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

/* Vertical Group Styling */
.ui\:btn-group-vertical > .ui\:btn:not(:first-child) {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  margin-top: -1px; /* Overlap borders */
}

.ui\:btn-group-vertical > .ui\:btn:not(:last-child) {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

/* ========================================================================
   Effect: Ripple
   ======================================================================== */

.ui\:btn {
  /* These are already in your button styles but are essential for the ripple */
  position: relative;
  overflow: hidden;
}

.ui\:ripple {
  position: absolute;
  border-radius: 50%;
  background-color: currentColor;
  opacity: 0.2;
  transform: scale(0);
  animation: ripple 600ms linear;
  pointer-events: none;
}

@keyframes ripple {
  to {
    transform: scale(4);
    opacity: 0;
  }
}

/* ========================================================================
   Toast Animations
   ======================================================================== */

@keyframes toast-enter {
  0% {
    transform: translate3d(100%, 0, 0) scale(0.6);
    opacity: 0;
  }
  100% {
    transform: translate3d(0, 0, 0) scale(1);
    opacity: 1;
  }
}

@keyframes toast-leave {
  0% {
    transform: translate3d(0, 0, 0) scale(1);
    opacity: 1;
  }
  100% {
    transform: translate3d(100%, 0, 0) scale(0.6);
    opacity: 0;
  }
}

.animate-enter {
  animation: toast-enter 0.35s cubic-bezier(.21,1.02,.73,1) forwards;
}

.animate-leave {
  animation: toast-leave 0.4s forwards cubic-bezier(.06,.71,.55,1);
}

/* Toast container styling */
.toast-container {
  z-index: 9999;
}
